<template>
  <div>
    <LayoutBox one="通知">
      <el-form size="small" inline :model="searchJson">
        <el-form-item label="时间">
          <el-date-picker v-model="searchJson.timeBegin" type="datetime" placeholder="选择日期时间" />
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="apiGetFeedback">查询</el-button>
        </el-form-item>
      </el-form>
      <el-table :data="feedbackList" style="width: 100%" size="small">
        <el-table-column prop="createTime" label="日期" />
        <el-table-column prop="describes" label="描述" />
        <el-table-column prop="reason" label="原因" />
        <el-table-column prop="reason" label="图片">
          <template slot-scope="scope">
            <el-image class="feedback_img" fit="cover" :src="scope.row.imageUrl" />
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页 -->
      <el-pagination
        class="mt-1 text-right"
        background
        layout="total, sizes, prev, pager, next, jumper"
        :current-page="queryInit.pageNo"
        :page-sizes="[10, 20, 30, 40]"
        :page-size="queryInit.pageSize"
        :total="total"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
      />
    </LayoutBox>
  </div>
</template>

<script>
import { MIXINS_GETFEEDBACK_API } from '@/mixins/api.js'
import LayoutBox from '@/components/LayoutBox/index'

export default {
  name: 'feedbackList',

  components: {
    LayoutBox
  },

  mixins: [
    MIXINS_GETFEEDBACK_API,
  ],

  data () {
    return {
      feedbackList: [], // 建议反馈列表
      queryInit: { // 分页初始化
        pageNo: 1,
        pageSize: 10,
      },
      total: 0, // 总页数
      searchJson: { // 搜索数据
        timeBegin: '',
      },
    }
  },

  async created () {
    await this.apiGetFeedback()
  },

  methods: {
    handleSizeChange (pageSize) {
      this.queryInit.pageSize = pageSize
    },
    handleCurrentChange (pageNo) {
      this.queryInit.pageNo = pageNo
    },
    // 私有方法-------------------------------------------
    // 查询意见反馈
    async apiGetFeedback () {
      const data = {
        ...this.queryInit,
        ...this.searchJson
      }
      const res = await this.getFeedBack(data)
      if (res.data.success) {
        this.feedbackList = res.data.data.list
        this.total = res.data.data.total
      } else {
        this.$message.error(res.data.msg)
      }
    }
  }
}
</script>

<style lang="scss" scoped>
  .feedback_img{
    width: 80px;
    height: 80px;
  }
</style>
